import React, { useContext } from 'react';
import "@lottiefiles/lottie-player";
import '../sass/NotFound.scss';
import Context from '../services/Context';
import styled from 'styled-components';

export default function NotFoundComponent() {
  const { theme } = useContext(Context);

  const Div = styled.div`
    background: ${props => props.theme[`transitionLinear${theme}`]};
  `;

  return (
    <Div className="not-found-body">
      <lottie-player
        autoplay
        background="transparent"
        speed="1"
        loop
        mode="normal"
        src="https://assets1.lottiefiles.com/private_files/lf30_3X1oGR.json"
        style={{"width": "500px", "margin": '0 auto'}}
      />
      <div className="not-found-text">
        <h2 className='center-title'>Pgina no encontrada - erro: 404</h2>
        <p className="not-found-p">
          Poxa a pgina que tentou acessar no foi encontrada, mas no desanime! Que tal aproveitar nossa explorao e olhar os projetos em destaque?
        </p>
      </div>
    </Div>
  );
}
